import React, { Suspense, lazy } from "react";
import { isTagEmbed } from "../../../../actions/api";
const Author = lazy(() => import("../../Elements/Author/Author"));
const Time = lazy(() => import("../../Elements/Time/Time"));
const Network = lazy(() => import("../../Elements/Network/Network"));

const AuthorDetails = ({ author, personalization, postTime, network, ThemeRule, font }) => {
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (personalization.postAuthor && !author.isInstaUser) ? true : false;
  const authorSeprator = (personalization.postAuthor && personalization.postTime && !author.isInstaUser) ? true : false;
  return <div className="tb_sp_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    <div className="tb_sp_author">
      {postAuthor ? <Suspense fallback={<></>}><Author author={author} network={network} authorClass={'tb_sp_author_profile'} /></Suspense> : ''}
      <div className="tb_sp_author_info">
        {postAuthor ? <div className="tb_sp_authorname">{author.name}</div> : ''}
        <div className="tb_sp_post_info">
          {postAuthor ? <div className="tb_sp_username">{username}</div> : ''}
          {authorSeprator ? <div className="tb_sp_seprator"> </div> : ''}
          {personalization.postTime ? <Suspense fallback={<></>}><Time postTime={postTime} timeClass={`tb_sp_time tb-cTBfont-${ThemeRule.font_varient}`} authorColor={ThemeRule.authorColor} /> </Suspense> : ''}
        </div>
      </div>
    </div>
    {!isTagEmbed ?
      <div className="tb_sp_social_">
        <Suspense fallback={<></>}><Network networkClass={'tb_sp_social__ico'} network={network} isDefault={ThemeRule.iconType === 1 ? true : false} ThemeRule={ThemeRule} /></Suspense>
      </div> : null}
  </div>
}
export default AuthorDetails